@import "../partials/variables";

//Theme 13
#atw_style13{
    .atw__title13{
        margin-bottom: 50px;
        padding-bottom: 0;
        h3{
            position: relative;
            &:before{
                position: absolute;
                content: "";
                width: 35%;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                height: 1px;
                background: #e4e8ed;
            }
            &:after{
                position: absolute;
                content: "";
                width: 35%;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
                height: 1px;
                background: #e4e8ed;
            }
        }
        &::before{
            content: none;
        }
    }
    .atw--single_item{
        &:hover{
            .atw_item_top{
                .atw_overlay_content{
                    visibility: visible;
                    opacity: 1;
                    top: 0;
                }
            }
        }
        .atw_item_top{
            position: relative;
            overflow: hidden;
            .atw_overlay_content{
                position: absolute;
                width: 100%;
                height: 100%;
                left: 0;
                top: 20px;
                display: flex;
                align-items: center;
                justify-content: center;
                background: rgba(0,0,0,0.6);
                visibility: hidden;
                opacity: 0;
                transition: $transition;
                .atw_product_actions{
                    margin-bottom: 10px;
                    li{
                        padding: 0 2px;
                        a{
                            display: block;
                            height: 40px;
                            padding: 0 9px;
                            border: 1px solid #e4e4ed;
                            background: #fff;
                            padding-top: 12px;
                            color: #9192a3;
                            border-radius: 3px;
                        }
                    }
                }
                .atw_btn{
                    background: $theme4;
                    border-radius: 3px;
                }
            }
        }
        .atw_item_bottom{
            padding: 20px;
            .atw_item_header{
                padding: 0;
                .atw_item_cat{
                    font-size: 14px;
                    margin-bottom: 10px;
                }
                .atw_item_title{
                    margin-bottom: 10px;
                }
            }
            .atw_item_info{
                display: flex;
                justify-content: space-between;
                .atw_price{
                    color: $theme4;
                }
            }
            p{
                margin-top: 15px;
                padding-top: 15px;
                border-top: 1px solid $borderColor;
                color: #5a6671;
                font-size: 15px;
                line-height: 26px;
            }
        }
    }
}